import { get } from "svelte/store";
import { animationSpeed, language as selectedLanguage } from "../store/settings";
var utterance = null;
var rate = get(animationSpeed);
var language = get(selectedLanguage);
init();
function read(text) {
    utterance = new SpeechSynthesisUtterance(text);
    utterance.rate = rate;
    utterance.lang = language;
    utterance.onend = utterance.onerror = function () { return (utterance = null); };
    speechSynthesis.speak(utterance);
}
function endedReading() {
    return utterance === null;
}
function init() {
    animationSpeed.subscribe(onExecutionSpeedChanged);
    selectedLanguage.subscribe(onLanguageChanged);
}
function onExecutionSpeedChanged(animSpeed) {
    rate = animSpeed; // this value should be scaled properly
    if (utterance !== null) {
        utterance.rate = rate;
    }
}
function onLanguageChanged(lang) {
    language = lang;
    if (utterance !== null) {
        utterance.lang = language;
    }
}
export default {
    read: read,
    endedReading: endedReading
};
