import { get } from "svelte/store";
import { playAnimations, textToSpeech } from "../../store/settings";
export function ZERO_FLAG_SET(cache) {
    return cache["SW:Z"];
}
export function ZERO_FLAG_NOT_SET(cache) {
    return !cache["SW:Z"];
}
export function NEGATIVE_FLAG_SET(cache) {
    return cache["SW:N"];
}
export function NEGATIVE_FLAG_NOT_SET(cache) {
    return !cache["SW:N"];
}
export function IS_ANIMATING(cache) {
    return get(playAnimations);
}
export function TTS_ENABLED(cache) {
    return get(textToSpeech);
}
